<template>
    <div class="login">
        <div class="login-banner">
            <!-- logo -->
            <div class="login-logo">
                <h1>多方数据计算平台</h1>
            </div>
            <!-- 表 -->
            <el-form class="login-form" aria-autocomplete="on" label-position="left"
                ref="loginForm" :model="loginForm" :rules="rules">
                <div class="title-container">
                    <h2>用户登录</h2>
                </div>
                <el-form-item prop="account" :label-width="loginWidth" label="用户账号">
                    <el-input placeholder="用户账号"  v-model="loginForm.account"></el-input>
                </el-form-item>
                <el-form-item prop="password" :label-width="loginWidth" label="账号密码">
                    <el-input :type="pwdType" placeholder="账号密码" v-model="loginForm.password"></el-input>
                </el-form-item>
                <el-form-item prop="role" :label-width="loginWidth" label="用户角色">
                    <el-select style="width:100%" v-model="loginForm.role" placeholder="选择用户角色">
                            <el-option v-for="item in roleList"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id"></el-option>
                        </el-select>
                </el-form-item>

                <el-form-item prop="role" :label-width="loginWidth">
                        <el-button type="primary" class="login-btn" @click="toLogin('loginForm')">登录</el-button>
                </el-form-item>

                    <el-form-item prop="role" :label-width="loginWidth">
                            <el-button class="register-btn" @click="toRegister">注册</el-button>
                </el-form-item>
            </el-form>
        </div>
        
    </div>
</template>

<script>
export default {
    data(){
        return{
            loginWidth:'100px',
            pwdType:'password',
            loginForm:{
                account:'',
                password:'',
                role:'user'
            },
            roleList:[
                { name:'平台用户',id:'user'},
                { name:'平台管理员', id:'admin'},
                { name:'政府管理员',id:'gov'},
                { name:'浪潮管理员',id:'langChao'}
            ],
            rules:{
                account:[
                    { required:true,message:'请输入账号',trigger:'blur'}
                ],
                password:[
                    { required:true,message:'请输入密码',trigger:'blur'},
                    { min: 6, max: 16, message: '密码长度在 6 到 16 之间', trigger: 'blur' }
                ]
            }
        }
    },
    methods:{
        showPwd(){
            if(this.pwdType === 'password'){
                this.pwdType = 'text';
            }else if(this.pwdType === 'text'){
                this.pwdType = 'password';
            }
        },
        toLogin(formName){
            let that = this;
            const ToPath=function(role){
                switch (role){
                    case 'user':
                        that.$router.push('/index/user')
                        break;
                    case 'admin':
                        that.$router.push('/index/admin')
                        break;
                    case 'gov':
                        that.$router.push('/index/government')
                        break;
                    case 'langChao':
                        that.$router.push('/index/langchao')
                        break;
                }
            }
            this.$refs[formName].validate((valid)=>{
                if(valid){
                    that.$api.login({
                        account:that.loginForm.account,
                        password:that.loginForm.password,
                        role:that.loginForm.role
                    }).then(res=>{
                        let code = res.err_code;
                        if(code === 100){
                            sessionStorage.setItem('token',`Bearer ${res.data.token}`);
                            sessionStorage.setItem('role',that.loginForm.role);
                            that.$message({
                                message:'登录成功',
                                type:'success'
                            })
                            ToPath(that.loginForm.role);
                        }else if(code === 101){
                            that.$message({
                                message:'用户名错误',
                                type:'error'
                            })
                        }else{
                            that.$message({
                                message:'登录错误',
                                type:'error'
                            })
                        }
                    })
                }
            })
        },
        toRegister(){
            this.$router.push('/auth/register');
        }
    }
}
</script>

<style lang="scss" scoped>
</style>